.Header {
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-top: 30px;
  background-color: rgb(95, 173, 91);  
  width: 90%;
  padding: 10px 18px;
  -webkit-box-shadow: 4px 2px 24px 0px #888888;
          box-shadow: 4px 2px 24px 0px #888888;
  border-bottom-right-radius: 20px;
  font-size: 2.5em;
  padding-left: 28px;
  min-height: 50px;
  position: fixed;
}
.header-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.counter-box {
  margin-left: 15px;
  border-radius: 50px;
  padding: 0 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(255, 255, 255, 0.884);
  width: 45px;
  height: 45px;
  color: gray;
  border: none;
  font-size: 0.5em;
}
.counter-box:hover {
  background-color: rgba(128, 128, 128, 0.411);
  color: white;
}
.counter-box:first-of-type {
  margin-left: -30px;
}
.header-buttons {
  display: -ms-flexbox;
  display: flex;
  margin-top: -10%;
}

@media only screen and (max-width : 800px) {
  .Header {
    -ms-flex-direction: column;
        flex-direction: column;
    width: 80%;
    padding-bottom: 20px;
    position: initial;
    margin-bottom: 0;
  }
  .header-left {
    margin-bottom: 10px;
  }
  .header-right {
    width: 100%;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
  .counter-box {
    margin-bottom: -90px;
  }
  .header-buttons {
    margin-top: 30px;
  }
}



