
.task-back {
  /* background-color: rgba(201, 231, 231, 0.87); */
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 20px;
}
.task-back .task-top {
  margin-bottom: 20px;
}
.task-back input {
  border: none;
  width: 98%;
  /* max-width: 98%; */
  padding: 4px 5px;
  margin: 5px 0;
}
.task-back input:focus {
  border: 1px solid rgb(228, 98, 98);
}
.task-back textarea {
  resize: none;
  padding: 8px;
  width: 98%;
  font-size: 1em;
  /* max-width: 98%; */
  height: 150px;
}

.task-back .inputs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
}
.buttons {
  padding: 0 20px;
  width: 20%;
  min-width: 180px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.buttons button {
  background-color: rgba(78, 216, 240, 0.795);
  border: none;
  color: white;
  font-size: 1em;
  border-radius: 4px;
  margin: 5px 2.5px;
  padding: 5px;
  width: 80%;
}
.buttons button:hover {
  background-color: rgba(149, 219, 231, 0.541);
  color: rgb(146, 143, 143);
}
.buttons button.update-cancel {
  background-color: rgba(236, 210, 91, 0.795);
}
.buttons button.update-cancel:hover {
  background-color: rgba(238, 222, 149, 0.74);
}
@media only screen and (max-width : 800px) {
  .task-back textarea.description {
    width: 95%;
  }
  .task-back {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .buttons {
    margin-top: 15px;
  }
  .buttons button {
    width: 100%;
  }
}


