.Form {
  width: 89%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 30px;
  padding: 20px 30px;
  background-color: rgba(201, 231, 231, 0.87);
  border-top-left-radius: 20px;
  border-bottom-right-radius: 10px;
  -webkit-box-shadow: 4px 2px 34px 0px #888888;
          box-shadow: 4px 2px 34px 0px #888888;
}
.Form.hidden {
  visibility: hidden;
  height: 0;
  width: 0;
  margin-top: -30px;
}
.Form .form-buttons {
  width: 25%;
  border: none;
  padding: 5px 10px;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.Form .form-buttons > button {
  width: 100%;
  border: none;
  margin: 10px 0;
  padding: 8px 0;
  border-radius: 5px;
  background-color: rgb(228, 98, 98);
  /* background-color: rgba(228, 98, 98, 0.767); */
  color: white;
}
.Form .form-buttons > button:hover {
  color: rgb(228, 220, 220);
  background-color: rgba(228, 98, 98, 0.767);
}
.Form section.form-inputs {
  width: 70%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.Form section.form-inputs > input, textarea {
  font-family: 'Ubuntu', sans-serif;
  font-size: 0.8em;
  width: 100%;
  margin: 2px 0;
  border: none;
  border-radius: 4px;
  padding: 10px;
}
.Form section.form-inputs > input:focus, textarea:focus {
  border: 1px solid rgb(228, 98, 98);
}
.Form section.form-inputs > textarea {
  resize: none;
  height: 200px;
}


@media only screen and (max-width : 800px) {

  .Form {
    width: 85%;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 2%;
    margin: 0;
    -ms-flex-align: center;
        align-items: center;
  }
  .Form section.form-inputs {
    width: 100%;
    padding: 2%;
  }
  .Form section.form-inputs > input, textarea {
    width: 85%;
    margin: 5px 0;
  }
  .Form section.form-inputs > textarea {
    height: 200px;
  }
  .Form .form-buttons {
    width: 80%;
  }
  .Form .form-buttons > button {
    margin: 5px;
  }
}



